<main className="p-6 max-w-xl mx-auto">
  <h1 className="text-2xl font-bold mb-6 text-center">Клики по поисковому запросу</h1>

  <div className="bg-white p-6 rounded-2xl shadow-md space-y-4">
    <input
      type="text"
      placeholder="Поисковый запрос"
      className="border w-full p-2 rounded outline-blue-500"
      value={ask}
      onChange={(e) => setAsk(e.target.value)}
    />

    <div className="flex gap-2">
      <input
        type="date"
        className="border p-2 rounded w-1/2"
        value={from}
        onChange={(e) => setFrom(e.target.value)}
      />
      <input
        type="date"
        className="border p-2 rounded w-1/2"
        value={to}
        onChange={(e) => setTo(e.target.value)}
      />
    </div>

    <button
      onClick={handleSubmit}
      className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 w-full font-semibold"
    >
      Показать
    </button>
  </div>

  {count !== null && (
    <div className="text-lg font-semibold mt-6 text-center">
      Количество кликов: <span className="text-blue-600">{count}</span>
    </div>
  )}
</main>
